<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>专业管理</el-breadcrumb-item>
      <el-breadcrumb-item>专业信息</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- 搜索 -->
      <el-row :gutter="20">
        <el-col>
          <el-form ref="formRef" :model="queryVo" label-width="80px" inline>
            <el-form-item label="专业名称" prop="majorName">
              <el-input
                v-model="queryVo.majorName"
                @keyup.enter.native="queryPage"
                placeholder="请输入专业名称"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="所属学院" prop="deptName">
              <el-input
                v-model="queryVo.deptName"
                @keyup.enter.native="queryPage"
                placeholder="请输入所属学院"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" @click="queryPage"
                >搜索</el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>

      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button
            type="primary"
            icon="el-icon-plus"
              v-if="is_authority.is_user_add"
            size="mini"
            @click="open = true"
            >新增</el-button
          >
          
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
            @click="editUser"
            v-if="is_authority.is_user_upt"
            >修改</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            >删除</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            >导入</el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
            >导出</el-button
          >
          <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>

      <!-- 数据 -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          align="center"
          prop="majorName"
          label="专业名称"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="deptName"
          label="所属学院"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorEnrollingStatus"
          label="招生状态"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorStudyYear"
          label="学制"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorStudyMaxYear"
          label="允许修业年限"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorCategory"
          label="学科门类"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorEducation"
          label="学历"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorDemo"
          label="专业介绍"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="academicDegree"
          label="授予学位"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorApplyTime"
          label="专业申请年份"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorEnrollTime"
          label="专业招生年份"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorNativeName"
          label="校内专业名称"
        ></el-table-column>

        <el-table-column label="操作" width="120px" align="center">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>

            
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!-- 对话框 :before-close="handleClose" 在对话框关闭之前执行-->
      <!-- 标题 -->
      <el-dialog
        title="编辑"
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        <!-- 内容主体 -->

        <el-form
          :model="Form"
          :rules="rules"
          ref="formRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="专业名称" prop="majorName">
                <el-input
                  v-model="Form.majorName"
                  placeholder="请输入专业名"
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="专业id" prop="majorId">
                <el-input
                  v-model="Form.majorId"
                  placeholder="请输入专业id"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="学制" prop="majorStudyYear">
                <el-input
                  v-model="Form.majorStudyYear"
                  placeholder="请输入学制"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="允许修业年限" prop="majorStudyMaxYear">
                <el-input
                  v-model="Form.majorStudyMaxYear"
                  placeholder="请输入允许修业年限"
                ></el-input>
              </el-form-item>
            </el-col>
            
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="授予学位" prop="academicDegree">
                <el-input
                  v-model="Form.academicDegree"
                  placeholder="请输入授予学位"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="招生状态" prop="majorEnrollingStatus">
                <el-input
                  v-model="Form.majorEnrollingStatus"
                  placeholder="请输入招生状态"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="学历" prop="majorEducation">
                <el-select v-model="Form.majorEducation" placeholder="请选择学历">
                  <el-option
                    label="博士研究生"
                    value="博士研究生">
                  </el-option>
                  <el-option
                    label="硕士研究生"
                    value="硕士研究生">
                  </el-option>
                  <el-option
                    label="本科"
                    value="本科">
                  </el-option>
                  <el-option
                    label="专科"
                    value="专科">
                  </el-option>
                  <el-option
                    label="高中及其以下"
                    value="高中及其以下">
                  </el-option>
                  <el-option
                    label="其他"
                    value="其他">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="专业介绍" prop="majorDemo">
                <el-input
                  v-model="Form.majorDemo"
                  placeholder="请输入专业介绍"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="24">
              <el-form-item label="校内专业名称" prop="majorNativeName">
                <el-input
                  v-model="Form.majorNativeName"
                  placeholder="请输入校内专业名称"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="专业招生年份" prop="majorEnrollTime">
                <el-input
                  v-model="Form.majorEnrollTime"
                  placeholder="请输入专业招生年份"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="专业申请年份" prop="majorApplyTime">
                <el-input
                  v-model="Form.majorApplyTime"
                  placeholder="请输入专业申请年份"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="学院" prop="deptId">
                <el-select
                  v-model="Form.deptId"
                  placeholder="请输入内容"
                  filterable=""
                >
                  <el-option
                    v-for="item in deptOptions"
                    :key="item.id"
                    :label="item.deptName"
                    :value="item.deptId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="学科门类 " prop="disId">
                <el-select
                  v-model="Form.disId"
                  placeholder="请输入内容"
                  filterable=""
                >
                  <el-option
                    v-for="item in subOptions"
                    :key="item.id"
                    :label="item.disName"
                    :value="item.disId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <!-- 按钮 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="open = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      subOptions: [],
      deptOptions: [],
      Form: {},
      open: false,
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      List: [],
      queryVo: {
        majorName: undefined,
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      rules: {
        deptId: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        disId: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        majorName: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        majorId: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
    this.getTreeselect()
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    async getTreeselect() {
      const { data: res } = await this.$http.post(
        '/disciplineInfo/queryDisciplineInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const { data: res1 } = await this.$http.post(
        '/deptInfo/queryDeptInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      this.deptOptions = res1.data.dataList.records
      //  console.log(res.data.dataList.records);
      const a = res.data.dataList.records
      this.subOptions = this.unique(a)
      if (res.code != 20000) return this.$message.error(res.message)
      // console.log(this.deptOptions)
      // console.log(this.subOptions)
    },

    unique(arr) {
      const res = new Map()
      return arr.filter(arr => !res.has(arr.disName) && res.set(arr.disName, 1))
    },
    submitForm() {
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/majorInfo/saveOrUpdateMajorInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.queryPage()
          return this.$message.success(res.message)
        }
      })
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    async editUser(row) {
      this.open = true
      const id = row.id || this.ids
      console.log(id)
      const { data: res } = await this.$http.get(
        '/majorInfo/getByIdMajorInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      console.log(res)
      this.Form = res.data.entity
    },

    del(row) {
      if (row.majorId) {
        var a = row.majorId.split(',')
      }
      const userid = a || this.ids
      console.log(userid)
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete('/majorInfo/removeByIdMajorInfos', {
            data: userid
          })
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },
    async queryPage() {
      const { data: res } = await this.$http.post(
        '/majorInfo/queryMajorInfoList',
        this.queryVo
      )
      console.log(res.data.dataList.records)
      if (res.code != 20000) return this.$message.error(res.message)
      this.List = res.data.dataList.records
      this.page.total = res.data.dataList.total
      this.page.current = res.data.dataList.current
      this.page.size = res.data.dataList.size
      // console.log(this.List)
    },
    //选择框
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    //更新状态
    async uptStatus(status) {
      const { data: res } = await this.$http.put(
        `/jxyxUser/users/${status.id}/state/${status.mgState}`
      )
      if (res.code != 20000) {
        status.mgState = !status.mgState
        return this.$message.error(res.message)
      }
      return this.$message.success(res.message)
    },
    // 监听每页展示的条数
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //监听页码
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
